const SpeechRecognition =
  (window as any).SpeechRecognition || (window as any).webkitSpeechRecognition;
const SpeechGrammarList =
  (window as any).SpeechGrammarList || (window as any).webkitSpeechGrammarList;
const SpeechRecognitionEvent =
  (window as any).SpeechRecognitionEvent ||
  (window as any).webkitSpeechRecognitionEvent;

const recognition = new SpeechRecognition();
recognition.continuous = true;
recognition.lang = "en-US";
recognition.interimResults = false;
recognition.maxAlternatives = 1;

async function listen() {
  return new Promise<string>((resolve, reject) => {
    recognition.onresult = (event: any) => {
      console.log("done listening", event.results[0][0].transcript);
      recognition.onend = undefined;
      recognition.stop();
      resolve(event.results[0][0].transcript);
    };
    recognition.onerror = (err: Error) => {
      console.log("restarting listener due to error event", err);
      recognition.onend = undefined;
      recognition.stop();
      setTimeout(() => recognition.start(), 100);
    };
    recognition.onend = () => {
      console.log("restarting listener due to end event");
      recognition.start();
    };

    try {
      recognition.start();
    } catch (err) {}
  });
}

const SLEEP_COMPLETE = Symbol("SLEEP_COMPLETE");
function sleep(ms: number) {
  return new Promise<Symbol>((resolve) =>
    setTimeout(() => resolve(SLEEP_COMPLETE), ms)
  );
}

