import { AudioConfig, SpeechConfig, SpeechSynthesisBookmarkEventArgs, SpeechSynthesisOutputFormat, SpeechSynthesizer } from 'microsoft-cognitiveservices-speech-sdk';
import { emojiMap, toneMap } from './toneMap';
import _emojiRegex from 'emoji-regex';
import { AZURE_SUBSCRIPTION } from './apiKeys';
const emojiRegex = _emojiRegex();

const speechConfig = SpeechConfig.fromSubscription(AZURE_SUBSCRIPTION, 'westus');
speechConfig.speechSynthesisOutputFormat = SpeechSynthesisOutputFormat.Audio48Khz96KBitRateMonoMp3
speechConfig.speechSynthesisLanguage = "en-US"; 
const VOICE = 'SaraNeural'; // SaraNeural
speechConfig.speechSynthesisVoiceName = `en-US-${VOICE}`
const audioConfig = AudioConfig.fromDefaultSpeakerOutput();

interface AudioOutputCallback {
  bookmark: string;
  offset: number; // in ms
  callback: () => void;
}
interface AudioOutputSegment {
  startedPlaying?: number; // in ms, get using window.performance.now()
  duration: number; // in ms
  callbacks: AudioOutputCallback[];
}
const audioOutputQueue: AudioOutputSegment[] = [];
function playNextAudioSegment() {
  if (audioOutputQueue.length === 0) return;
  const segment = audioOutputQueue[0];
  console.log('setting up callbacks', segment.callbacks)
  for (const callback of segment.callbacks) {
    setTimeout(callback.callback, callback.offset);
  }
  segment.startedPlaying = window.performance.now();
  setTimeout(() => {
    audioOutputQueue.shift();
    playNextAudioSegment();
  }, segment.duration);
}

function addAudioToQueue(duration: number, callbacks: AudioOutputCallback[]) {
  audioOutputQueue.push({duration, callbacks});
  if (audioOutputQueue.length === 1) {
    playNextAudioSegment();
  }
}

let currentStopCallback: () => void = () => {}

export function waitForSpeechCompletion() {
  return new Promise<void>((resolve => {
    if (playing) {
      currentStopCallback = resolve;
    } else {
      currentStopCallback = () => {}
      resolve();
    }
  }))
}

let currentInterval = 100;
let previousTime = 0;
let playing = false;
setInterval(() => {
  const newTime = (audioConfig as any).privDestination.privAudio.currentTime
  if (newTime !== previousTime && !playing) {
    console.log('start');
    playing = true;
    currentInterval = 10;
  } else if (newTime === previousTime && playing) {
    console.log('stopped');
    playing = false;
    currentStopCallback();
  }
  previousTime = newTime;
}, 10)

function encodeEmoji(emojiString: string) {
  let utf8Array = new TextEncoder().encode(emojiString);
  let base64url = btoa(String.fromCharCode(...utf8Array))
      .replace(/\+/g, '-')
      .replace(/\//g, '_')
      .replace(/=+$/, '');
  return base64url;
}
function decodeEmoji(base64url: string) {
  base64url = base64url
      .replace(/-/g, '+')
      .replace(/_/g, '/');
  let padding = base64url.length % 4;
  if (padding) {
      base64url += '='.repeat(4 - padding);
  }
  let utf8Array = Uint8Array.from(atob(base64url), c => c.charCodeAt(0));
  let emojiString = new TextDecoder().decode(utf8Array);
  return emojiString;
}

const synthesizer = new SpeechSynthesizer(speechConfig, audioConfig);

const bookmarkListeners: {[key: string]: (eventArgs: SpeechSynthesisBookmarkEventArgs) => void} = {}

synthesizer.bookmarkReached = (_synthesizer, bookmark) => {
  console.log('registering bookmark', bookmark.text)
  bookmarkListeners[bookmark.text]?.(bookmark);
}

let currentTone: string | undefined = undefined;
let rawEmotion: string | undefined = undefined;

const negativeTones = ['sad', 'terrified', 'angry', 'unfriendly']
const positiveTones = ['excited', 'cheerful', 'friendly']
let toneSwitchCount = 0;
let messageCount = 0;

export function synthesizeSpeech(text: string, setEmoji: (newEmoji: string) => void, ignoreTone = false) {
  messageCount++;
  const messageId = messageCount;
  const bookmarks: string[] = [];

  return new Promise<void>((resolve, reject) => {
    let ssml = '';
    if (currentTone) {
      toneSwitchCount++;
      ssml += `<mstts:express-as style="${currentTone}">`
      ssml += `<s /><mark name="tone_${currentTone}_${toneSwitchCount}" />`
      bookmarks.push(`tone_${currentTone}_${toneSwitchCount}`);
    }
    console.log(text)
    let innerText = (
      text
        .replace(/\<([^\>]*)\>/g, (_wholeTag: string, innerTag: string) => {
          let replacement = '';

          let oldTone = currentTone;

          if (currentTone) {
            currentTone = undefined;
            replacement += '</mstts:express-as>\n';
          }

          if (!innerTag.startsWith('/') && innerTag) {
            rawEmotion = innerTag
            let newTone: string | undefined = toneMap[rawEmotion]

            // if (newTone === 'excited') {
            //   newTone = 'cheerful'
            // }

            // firstWords = false;

            // // can't be overly positive
            // if (positiveTones.includes(newTone) && oldTone && positiveTones.includes(oldTone)) {
            //   newTone = 'cheerful';
            // }

            // no mood swings
            if (positiveTones.includes(newTone) && oldTone && negativeTones.includes(oldTone)) {
              newTone = undefined;
            }

            if (newTone) {
              currentTone = newTone
              toneSwitchCount++;
              replacement += `<mstts:express-as style="${currentTone}">`
              replacement += `<s /><mark name="tone_${currentTone}_${toneSwitchCount}" />`
              bookmarks.push(`tone_${currentTone}_${toneSwitchCount}`);
            } else {
              console.log('tone lookup failure', newTone)
            }
          }

          return replacement;
        })
        .replace(/(:\))+/g, '😂')
        .replaceAll(emojiRegex, (emoji) => {
          toneSwitchCount++;
          const mark = `emoji_${encodeEmoji(emoji)}_${toneSwitchCount}`
          console.log('adding emoji bookmark', mark)
          bookmarks.push(mark);
          return `<s /><mark name="${mark}" />`
        })
    )

    ssml += innerText;

    if (currentTone) {
      ssml += '</mstts:express-as>'
    }

    ssml = `
      <!--ID=B7267351-473F-409D-9765-754A8EBCDE05;Version=1|{"VoiceNameToIdMapItems":[{"Id":"38db11b6-fa64-4989-8d75-4a48695ee5cd","Name":"Microsoft Server Speech Text to Speech Voice (en-US, ${VOICE})","ShortName":"en-US-${VOICE}","Locale":"en-US","VoiceType":"StandardVoice"}]}-->
      <!--ID=FCB40C2B-1F9F-4C26-B1A1-CF8E67BE07D1;Version=1|{"Files":{}}-->
      <!--ID=5B95B1CC-2C7B-494F-B746-CF22A0E779B7;Version=1|{"Locales":{"en-US":{"AutoApplyCustomLexiconFiles":[{}]}}}-->
      <speak xmlns="http://www.w3.org/2001/10/synthesis" xmlns:mstts="http://www.w3.org/2001/mstts" xmlns:emo="http://www.w3.org/2009/10/emotionml" version="1.0" xml:lang="en-US">
        <voice name="en-US-${VOICE}">
          <s /><mark name="message_begin_${messageId}" />
          ${ssml}
          <s /><mark name="message_end_${messageId}" />
        </voice>
      </speak>
    `.trim();

    bookmarks.push(`message_begin_${messageId}`);
    bookmarks.push(`message_end_${messageId}`);

    let hasUpdatedEmoji = false;

    let bookmarkCallbacks: AudioOutputCallback[] = [];

    const onBookmarkTriggered = (bookmark: string) => {
      console.log('hit bookmark', bookmark)
      if (/^tone_[a-z]+_[0-9]+$/.test(bookmark)) {
        console.log('Tone:', bookmark.split('_')[1])
        const toneEmoji = emojiMap[bookmark.split('_')[1]]
        if (toneEmoji && !hasUpdatedEmoji && !ignoreTone) {
          // setEmoji(emojiMap[bookmark.split('_')[1]])
        }
      } else if (/^emoji_.+_[0-9]+$/.test(bookmark)) {
        const encodedEmoji = bookmark.replace(/^emoji_/, '').replace(/_[0-9]+$/, '')
        console.log('Emoji:', encodedEmoji)
        setEmoji(decodeEmoji(encodedEmoji))
        hasUpdatedEmoji = true;
      } else if (bookmark === `message_begin_${messageId}`) {
        console.log('Start:', text)
      } else if (bookmark === `message_end_${messageId}`) {
        console.log('End:', text, resolve)
        resolve();
      }
    }
    const onBookmarkRegistered = (bookmark: string, eventArgs: SpeechSynthesisBookmarkEventArgs) => {
      bookmarkCallbacks.push({
        bookmark: bookmark,
        offset: eventArgs.audioOffset / 10000,
        callback: () => onBookmarkTriggered(bookmark)
      })
    }
    for (let bookmark of bookmarks) {
      bookmarkListeners[bookmark] = (eventArgs: SpeechSynthesisBookmarkEventArgs) => {
        onBookmarkRegistered(bookmark, eventArgs)
      }
    }

    synthesizer.speakSsmlAsync(ssml, (result) => {
      console.log('speak ssml result', result, ssml)
      if (!result.audioDuration) {
        bookmarkCallbacks = bookmarks.map(bookmark => ({
          bookmark: bookmark,
          offset: 0,
          callback: () => {
            onBookmarkTriggered(bookmark)
          }
        }))
      }
      addAudioToQueue(result.audioDuration / 10000, bookmarkCallbacks)
    }, (err) => {
      console.error('speakSsmlAsync error', err)
      reject(err)
    });

    // if (innerText) {      
    //   console.log('speak ssml', ssml)
    //   synthesizer.speakSsmlAsync(ssml, (result) => {
    //     speechQueue.push({
    //       text: ssml,
    //       audioDuration: result.audioDuration,
    //       callback: resolve
    //     })
    //     if (speechQueue.length === 1) {
    //       // console.log('processQueueItem starting fresh', speechQueue[0])
    //       processQueueItem();
    //     }
    //   });
    // } else {
    //   resolve();
    // }
  })
}
