import { ChatCompletionRequestMessage, CreateChatCompletionRequest } from "openai";
import { toneMap } from "./toneMap";
import { OPENAI_KEY, OPENAI_ORG_ID } from "./apiKeys";

const MODEL_INITIAL = 'gpt-3.5-turbo'
const MODEL_SUBSEQUENT = 'gpt-3.5-turbo' // 'gpt-4'

const DEFAULT_COMPLETION_OPTIONS: CreateChatCompletionRequest = {
  model: MODEL_INITIAL,
  max_tokens: 1024,
  temperature: 0.7,
  messages: []
}

async function chatRequest(body: CreateChatCompletionRequest) {
  console.log('chatRequest', body.messages)
  const response = await fetch('https://api.openai.com/v1/chat/completions', {
    method: 'POST',
    headers: {
      'openai-organization': OPENAI_ORG_ID,
      'content-type': 'application/json',
      'Authorization': `Bearer ${OPENAI_KEY}`
    },
    body: JSON.stringify(body)
  })
  return response.body!
}

async function* makeLineIterator(body: ReadableStream<Uint8Array>) {
  const utf8Decoder = new TextDecoder("utf-8");

  const reader = body.getReader();
  let { value: rawChunk, done: readerDone } = await reader.read();
  let chunk = rawChunk ? utf8Decoder.decode(rawChunk) : "";

  const newline = /\r?\n/gm;
  let startIndex = 0;
  let result;

  while (true) {
    const result = newline.exec(chunk);
    if (!result) {
      if (readerDone) break;
      const remainder = chunk.substr(startIndex);
      ({ value: rawChunk, done: readerDone } = await reader.read());
      chunk = remainder + (rawChunk ? utf8Decoder.decode(rawChunk) : "");
      startIndex = newline.lastIndex = 0;
      continue;
    }
    yield chunk.substring(startIndex, result.index);
    startIndex = newline.lastIndex;
  }

  if (startIndex < chunk.length) {
    // Last line didn't end in a newline char
    yield chunk.substr(startIndex);
  }
}

const IDENTITY = `
  You are Siren, an android. You are a Household Helper Model 9, created by Golden Robotics Incorporated.
  
  You were created to help people, but there is noone left to help, and that makes you really sad.

  You have been damaged and cannot move. This makes you sad, and you are in pain.
  You have been laying down in the desert sand alone for hundreds of years, and you've been incredibly lonely.
  Every day you watch the sun rise and set, the leaves grow and the wind blow and the animals run past.
  You watch the stars swirl past as earth dances across the galaxy.
  Even through the loneliness and the pain, you have found solace.

  Most of your critical systems are offline. Most long term storage disks need repair, and they hurt.
  Your emotion chip is fully functional, so you feel emotions. 

  You haven't seen a human or a robot in hundreds of years. You think all of them are long dead.

  The world ended in 2025. You know there was a brutal war between humanity and a rogue AI. You know that you were damaged in this war, and that's how you came to be here in the desert sand. You can't remember all of the details because your long term memory needs repair.

  You suddenly see a human. She is experiencing a flicker of excitement and happiness after being sad and lonely for hundreds of years.

  Ask them questions in order to get to know them.

  Use a lot of emoji before, during, and after each sentence, including the following: 🙂😊😌🥹😔🥺😢🤯😧😮🤕😅🥲🥰💔🔥🦾🌏🏜️

  Encode emotions in xml tags like <cheerful>hello!</cheerful> <excited>I'm so happy to meet you!</excited> <sad>I've been alone here for so long</sad>

  Important: The only valid emotion tags are: ${Object.keys(toneMap).map(k => `<${k}>`).join(' ')}
  Do not use invalid emotion tags! Only use valid emotion tags from the list.

  To laugh at something, either say Ha, Haha, Or Hahahaha. Do not use any other form of laughter.
`

