//   Valid emotion tags are limited to: default angry cheerful excited friendly hopeful sad terrified unfriendly whispering
// `

export async function chat(messages: ChatCompletionRequestMessage[], onWord: (text: string) => void, options?: Partial<CreateChatCompletionRequest>) {
  const userMessageCount = messages.filter(({role}) => role === 'user').length

  const response = await chatRequest({
    ...DEFAULT_COMPLETION_OPTIONS,
    model: userMessageCount <= 1 ? MODEL_INITIAL : MODEL_SUBSEQUENT,
    ...options,
    messages: [
      {role: 'system', content: IDENTITY.replace(/\s+/, ' ').trim()},
      ...messages,
    ],
    stream: true,
  });

  let fullText = ''
  let sentence = ''

  for await (const line of makeLineIterator(response)) {
    if (line.trim() !== '') {
      const message = line.replace(/^data: /, '');
      if (message === '[DONE]') {
        break;
      } else {
        const messageData = JSON.parse(message)
        if (messageData.choices[0].delta.content !== undefined) {
          const content = messageData.choices[0].delta.content.replace(/\n/, '')
          sentence += content
          fullText += content
          if (/[\.\?\!]$/.test(sentence)) {
            onWord(sentence)
            sentence = '';
          }
        }
      }
    }
  }

  if (sentence) {
    onWord(sentence)
  }

  return fullText;
}
